.app-wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "h h" "n c";
  grid-template-rows: 70px 1fr;
  grid-template-columns: 2fr 10fr;
}

body {
  background-color: #FAF8F3;
}

.app-wrapper-content {
  grid-area: c;
  background: rgb(159,157,153);
  background: linear-gradient(315deg, rgba(159,157,153,1) 0%, rgba(210,191,191,1) 35%, rgba(224,228,209,1) 54%, rgba(205,231,211,1) 65%, rgba(202,238,245,1) 100%);
  position: relative;
  padding: 20px;
}